.button{
    height:50px;
    width:19vw;
    margin: 2px;
}

input[type="button"]{
    width:100%;
    height: 100%;
    text-align: center;
    border:none;
    color: white;
    background: #3b3b3b;
    cursor: pointer;
    border-radius: 5px;
}

input[type="button"]:hover{
    background-color: #2a2a2a;
}

input[type="button"]:active{
    background: #000000;
}

